// THEME CONFIG

// global variables, used in mixins and styling
@import "theme/variables";
@import "theme/fonts";
// ==================================================================================== [2]

// LOAD TOOLS
@import "tools/mixins";
// grid - breakpoints and column widths,
// they determine template's behaviour on various screens
@import "tools/grid";
// external plugins
@import "plugins/select2/core";

