// so give them recognizable, meaningful names
// which should be easy to read and use in development.
// When the elements are semantically named,
// you can manipulate their "physical" behaviour on the page,
// using positions, floating, padding, and nesting.

// [TIP]

// Save your code structure in 'skeleton' directory.
// Put there some files including clean code structure with selectors and empty brackets.
