// ==================================================================================== [4]

// LOAD GLOBAL STYLES

// Here we're starting with defining final look
// of our properly named, kept in precise structure of selectors,
// fully reusable and responsive elements.

// The below rules define some universal properties
// often used in styles for pages, concerning the DOM.
// This part of code makes our template unique.

@import "theme/global";

// ==================================================================================== [5]

// SET UP SOME SEXY STYLING FOR COMMON ELEMENTS!

// The below styles will define core elements' look
// and make our template more coherent.
// If you want to describe some default buttons
// by setting them some paddings, margins or colors,
// you should write it here.


// [TIP]

// Don't be too precise, because styles assigned to specific
// pages will be loaded later.
// Describe common elements using meaningful classnames:
// '.button', '.button-big', '.button-small' etc.


// [TIP]

// It's good to provide some universal collections of properties
// That's why the files are placed in 'factories' folder.
// Keep classes in factories as low-responsible as possible
// - that's what makes them easy to extend!


// [TIP]

// You can save structure of those default elements in 'skeleton' directory
// to make them easier to re-use.


// [EXAMPLE]

//// .button {
////    border-radius: 3px;
////    &.button-sharp {
////        border-radius: 0px;
////    }
//// }

@import "theme/factories/buttons";

// ==================================================================================== [6]

