/// @group theme

@import url('https://fonts.googleapis.com/css?family=Teko:300,500,700&subset=latin-ext');
$teko : 'Teko', 'Arial', sans-serif;


@mixin tekoLight {
  font-family: $teko;
  font-weight: 300;
}
@mixin tekoMedium {
  font-family: $teko;
  font-weight: 500;
}
@mixin tekoBold {
  font-family: $teko;
  font-weight: 700;
}

@import url('https://fonts.googleapis.com/css?family=Muli:300,400,700,900&subset=latin-ext');
$muli : 'Muli', 'Arial', sans-serif;


@mixin muliLight {
  font-family: $muli;
  font-weight: 300;
}
@mixin muliRegular {
  font-family: $muli;
  font-weight: 400;
}
@mixin muliBold {
  font-family: $muli;
  font-weight: 700;
}
@mixin muliBlack {
  font-family: $muli;
  font-weight: 900;
}