.btn{
    display: inline-block;
    text-align: center;
    box-sizing: border-box;
    text-decoration:none;
    white-space: nowrap;
    cursor:pointer;
    @include border-radius(25px);
    @include tekoLight;
    @include transition(0.2s, ease-in);
    &.btn-white {
        background-color: $white;
        color : $Color2;
    }
    &.btn-purple {
        background-color: $Color2;
        color : $white;
    }
    &.btn-green {
        background-color: $Color6;
        color : $white;
    }
    &.btn-lightgreen {
        background-color: $Color12;
        color : $white;
    }
    &.btn-darkblue {
        background-color: $Color9;
        color : $white;
    }
    &.btn-red {
        background-color: $Color1;
        color : $white;
    }
    &.btn-red2 {
        background-color: $Color14;
        color : $white;
    }
    &.btn-white-hover:hover{
        background-color: $white;
        color: $Color2;
    }
    &.btn-yellow-hover:hover{
        background-color: $Color4;
    }
    &.btn-red-hover:hover{
        background-color: $Color1;
        color: $white;
    }
    &.btn-purple-hover:hover{
        background-color: $Color2;
        color: $white;
    }
    &.btn-blue-hover:hover{
        background-color: $Color7;
        color: $white;
    }
    &.btn-black-hover:hover{
        background-color: $black;
        color: $white;
    }
    &.btn-darkred-hover:hover{
        background-color: $Color8;
        color: $white;
    }
    &.btn-grey {
        background-color: $Color3;
        color : $white;
    }
    &.btn-shadow {
        @include material-shadow;
        &:hover{
            @include material-shadow-lower;
        }
    }
    &.btn-hollow {
        @include box-shadow(0,0,0,1,$Color2);
    }
    &.btn-hollow-blue {
        color: $Color7;
        @include box-shadow(0,0,0,1,$Color7);
    }
    &.btn-hollow-darkred {
        color: $Color8;
        @include box-shadow(0,0,0,1,$Color8);
    }
    &.btn-hollow-black {
        color: $black;
        @include box-shadow(0,0,0,1,$black);
    }
}

@media (min-width: 320px){
    &.btn-small {
        @include fontLines(12px,14px);
        padding-top: 6px;
        padding-bottom: 2px;
    }
    &.btn-medium {
        @include fontLines(15px,17px);
        padding-top: 8px;
        padding-bottom: 4px;
    }
    &.btn-big {
        @include fontLines(16px,18px);
        padding-top: 10px;
        padding-bottom: 6px;
    }
    &.btn-narrow {
        padding-left: 30px;
        padding-right: 30px;
    }
    &.btn-wide {
        padding-left: 70px;
        padding-right: 70px;
    }
}

