.select2-selection--single {
  background-color: #fff;
  @include muliBlack;
  outline: none;

  @media (min-width: 320px){
    font-size:22px;
  }
  @media (min-width: 480px){
    font-size:36px;
  }

  .select2-selection__rendered {
    margin-bottom: 20px;
    color: $Color1;

  }

  .select2-selection__clear {
    cursor: pointer;
    float: right;
    font-weight: bold;
  }

  .select2-selection__placeholder {
    color: #999;
  }

  .select2-selection__arrow {
    height: 45px;

    left:auto;
    bottom: auto;
    width: 40px;

    &:before{
      content: '';
      height: 2px;
      width: 20px;
      background-color: $grey;
      transform: rotate(45deg);
      position: absolute;
      top: 60%;
      left: 3px;
    }

    &:after{
      content: '';
      height: 2px;
      width: 20px;
      background-color: $grey;
      transform: rotate(-45deg);
      position: absolute;
      top: 60%;
      right: 3px;
    }


    @media (min-width:320px){
      @include absolute(-10px,-20px,auto,auto);

      &:before{
        width: 15px;
        left: 8px;
      }

      &:after{
        width: 15px;
        right: 7px;
      }

    }
    @media (min-width:480px){
      @include absolute(0,-30px,auto,auto);
      &:before{
        width: 20px;
        left: 3px;
      }

      &:after{
        width: 20px;
        right: 3px;
      }
    }
    @media (min-width:768px){
      @include absolute(0,-40px,auto,auto);
    }



  }
}

&[dir="rtl"] {
  .select2-selection--single {
    .select2-selection__clear {
      float: left;
    }

    .select2-selection__arrow {
      left: 1px;
      right: auto;
    }
  }
}

&.select2-container--disabled {
  .select2-selection--single {
    background-color: #eee;
    cursor: default;

    .select2-selection__clear {
      display: none;
    }
  }
}

&.select2-container--open {
  .select2-selection--single {
    .select2-selection__arrow {
      b {
        border-color: transparent transparent #888 transparent;
        border-width: 0 4px 5px 4px;
      }
    }
  }
}
