.select2-selection--single {
  background-color: #fff;
  @include muliBold;
  @include fontLines(20px,32px);
  border-radius: 20px;
  border: 1px solid $grey;
  padding: 5px 10px;
  outline: none;
  z-index: 100;

  .select2-selection__rendered {
    color: $Color15;
  }

  .select2-selection__clear {
    cursor: pointer;
    float: right;
    font-weight: bold;
  }

  .select2-selection__placeholder {
    color: #999;
  }

  .select2-selection__arrow {
    height: 25px;

    position: absolute;
    top: 5px;
    right: 10px;
    left:auto;
    bottom: auto;

    width: 40px;

    &:before{
      content: '';
      height: 2px;
      width: 15px;
      background-color: $grey;
      transform: rotate(45deg);
      position: absolute;
      top: 60%;
      left: 7px;
    }

    &:after{
      content: '';
      height: 2px;
      width: 15px;
      background-color: $grey;
      transform: rotate(-45deg);
      position: absolute;
      top: 60%;
      right: 8px;
    }

  }
}

&[dir="rtl"] {
  .select2-selection--single {
    .select2-selection__clear {
      float: left;
    }

    .select2-selection__arrow {
      left: 1px;
      right: auto;
    }
  }
}

&.select2-container--disabled {
  .select2-selection--single {
    background-color: #eee;
    cursor: default;

    .select2-selection__clear {
      display: none;
    }
  }
}

&.select2-container--open {
  .select2-selection--single {
    .select2-selection__arrow {
      b {
        border-color: transparent transparent #888 transparent;
        border-width: 0 4px 5px 4px;
      }
    }
  }
}
